@import "variables";

@mixin inner {
  @include phone-lg {
    padding: 0 16px;
  }

  position: relative;
  display: flex;
  flex-direction: column;
  //flex-wrap: wrap;
  //justify-content: space-between;
  //max-width: $site-width;
  //max-width: 92vw;
  //height: inherit;
  //margin: 0 auto;
  //padding: 0 $site-gutter;
}

@mixin word-break {
  -ms-word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;

  @supports not (overflow-wrap: break-word) {
    word-wrap: break-word;
  }
}

@mixin section-title($font-weight: $regular, $font-size: $title-font-size) {
  font: $font-weight $font-size $font-family-secondary;
  color: $title-yellow;
  -webkit-background-clip: text;
  text-shadow: 0 3px 0 $title-shadow;
  -webkit-text-stroke: 2px #B68336;

  letter-spacing: -4px;
}

@mixin card-title($font-weight: 700, $font-size: 56px) {
  font: $font-weight $font-size $font-family-base;
  color: $title-yellow;
  -webkit-background-clip: text;
  text-shadow: 0 4px 0 #BF801F;
  //-webkit-text-stroke: 1px #B68336;

  letter-spacing: -4px;
}

