@import "src/scss/variables";
@import "src/scss/mixins";

.statsItem {
  position: relative;
  width: 335px;
  height: 187px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: $card-border-radius;
  background: $card-background;
  //box-shadow: 0 4px 50px #0C0E05;

  &:after {
    content: "";
    position: absolute;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background: linear-gradient(0, rgba(248, 216, 128, 1), rgba(255, 255, 255, 1));
    border-radius: 28px;
    z-index: -1;
  }
}

.title {
  @include card-title;
}

.description {
  font-weight: $semibold;
}